ul li {
  font-size: 20px;
  border: 1px solid black;
  border-radius: 5px;
  position: relative;
  margin-bottom: 10px;
  padding: 5px 3px;
}

.fa-trash,
.fa-check-circle {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translate(0, -50%);
  cursor: pointer;
}

.fa-check-circle:hover {
  color: green;
}

.fa-trash:hover {
  color: red;
}