 @media(max-width: 414px) {
   div.wrapper {
     width: 80%;
     font-size: 13px;
   }

   input.write {
     font-size: 12px;
     margin-left: 0;
   }

   input.accept {
     font-size: 12px;
   }

   ul li {
     font-size: 16px;
   }

   p.negative {
     font-size: 30px;
   }
 }

 @media(max-width: 360px) {
   div.wrapper {
     padding-left: 40px;
   }

 }

 @media(max-width: 320px) {
   div.wrapper {
     width: 100%;
     font-size: 13px;
     height: 75%;
   }

   input.write {
     font-size: 12px;
     margin-left: 0;
   }

   input.accept {
     font-size: 12px;
   }

   ul li {
     font-size: 16px;
   }

   p.negative {
     font-size: 30px;
   }
 }