* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #ddd;
  font-family: 'Stylish', sans-serif;
}

h1 {
  font-family: 'Permanent Marker', cursive;
  margin-bottom: 15px;
  font-size: 220%;
}



div.wrapper {
  min-width: 30%;
  min-height: 60%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border: 3px solid black;
  padding-top: 40px;
  padding-left: 60px;
}